h1,h2,h3,h4, p{
   margin: 0;
   padding: 0;
}

ul{
    list-style: none;
}

a{
    text-decoration: none;
}

.hidden {
    width: 1px; 
    height: 1px; 
    margin: -1px; 
    border: 0; 
    padding: 0; 
    white-space: nowrap; 
    clip-path: inset(100%); 
    clip: rect(0 0 0 0); 
    overflow: hidden; 
}